.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: burlywood;
  margin: 0px 0px;
  height: 50px;
  font-size: 20px;
}
.nav-menu ul {
  display: flex;
  margin-right: 50px;
}
.nav-menu ul li {
  cursor: pointer;
  padding: 0px 20px;
  list-style: none;
  background-color: burlywood;
}
.nav-menu ul li:hover {
  background-color: yellowgreen;
}
