body.active-modal {
  overflow-y: hidden;
}
* {
  margin: 0;
  padding: 0;
  /* background-color: rgb(247, 238, 238); */
}
.App {
  font-family: sans-serif;
  text-align: center;
}
/* .container{

} */
.Render {
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 10px 50px;
}
navbar {
  display: flex;
  padding: 10px 10px;
  place-content: center;
}

section {
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
  /* display: flex; */
  margin: 30px 0px 0px 0px;
}

.img-content img {
  overflow: hidden;
  height: 200px;
  width: 250px;
  border-radius: 10px;
}
.submit-btn {
  position: absolute;
  bottom: 15px;
  left: 28px;
  padding: 5px 5px;
  border-radius: 1px solid rgb(216, 212, 212);
  cursor: pointer;
  /* border: 1px solid grey; */
}

.btn-modal {
  padding: 10px 20px;
  display: block;
  margin: 100px auto 0;
  font-size: 18px;
  cursor: pointer;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(49, 49, 49, 0.8);
}
.modal-content {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: #f1f1f1;
  padding: 14px 28px;
  border-radius: 3px;
  max-width: 600px;
  min-width: 300px;
}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 7px;
}
.close {
  position: absolute;
  bottom: 180px;
  left: 270px;
}
